#loadingMain{
    position: fixed;
    width: 100%;
    height: 100vh;
    background: var(--dark);
    animation: showPage 1.8s ease forwards 6.5s reverse;
    z-index: 9999;
}

#loadingName{
    width: 60vw;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    fill: transparent;
    -webkit-animation: fillAnim 1s ease forwards 5s;
            animation: fillAnim 1s ease forwards 5s;
}

#page{
    display: none;
}

.pageShown{
    -webkit-animation: showPage 2.5s ease forwards;
            animation: showPage 2.5s ease forwards;
}

#loadingName > path{
    white-space: pre;
    stroke: rgb(255, 255, 255);
    stroke-width: 5px;
    paint-order: stroke;
}

